.App {
  height: 100%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-title {
  font-size: 1.5em;
}
.App-intro {
  font-size: large;
}

.section {
  position: relative;
}

iframe {
  height: 300px;
  width: 100%;
}
.box-row {
}
.box {
  padding: 50px 30px;
  text-align: center;
  -webkit-box-shadow: 0 0 0 0 #ffffff;
  box-shadow: 0 0 0 0 #ffffff;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  border-radius: 5px;
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
      transform: translateY(0);
}
.box:hover {
  -webkit-box-shadow: 0 10px 30px -5px rgba(0, 0, 0, 0.1);
  box-shadow: 0 10px 30px -5px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
  -webkit-transform: translateY(-5px);
  -ms-transform: translateY(-5px);
      transform: translateY(-5px);
}
.box:hover .box-icon img {
  -webkit-filter: grayscale(0);
  filter: grayscale(0);
}
.box-icon {
  height: 160px;
  margin-bottom: 20px;
}
.box-icon img {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  max-width: 100%;
  height: 100%;
}
.box-header {
  text-transform: uppercase;
  margin: 0 0 10px;
  font-weight: 600;
}
.box-text {
}
.carousel-img-container {
  float: left;
  width: 40%;
  margin-left: 15px;
  margin-right: 15px;
  background-color: rgba(63, 171, 214, 0.75);
}
.carousel-img-container:not(:first-child) {
  margin-top: 15px;
}

.parallax {
  height: 100%;
}

.parallax div {
  height: 100%;
}


